
import moment from 'moment'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default{
mixins: [common,globals],
components: {
Flexbox,
XButton,
FlexboxItem
},
data () {
return {
info: {},
ready: false
}
},
mounted () {
this.init_data()
},
computed: {
sheduleDate () {
let result = ''
if(this.info.int_day){
result = moment(this.$filter('int_date')(this.info.int_day)).format('YYYY年MM月DD日')
}
return result
}
},
methods: {
init_data() {
let ca_id = this.$route.params.id
this.dr().getData('course_arranges?ca_id='+ca_id)
.then(data=>{
this.ready = true
this.info = data.list[0]
})
}
}
}



